<template>
    <div class="box">
        <navbar></navbar>
        <div class="content">
            <div class="center-box warp-1200 ov">
                <centerbar></centerbar>
                <div class="right fl">
                    <p class="balance"><span>内部转账记录</span></p>
                    <ul  v-infinite-scroll="load"  infinite-scroll-disabled="disabled" style="overflow:auto;height: 600px">
                        <div class="tit"><span>用户</span><span>金额</span><span>时间</span><span>状态</span></div>
                        <li v-for="item in transferList"><span>{{item.tousername}}</span><span>{{item.money}}usdt</span><span>{{item.create_time}}</span><span class="succ">转账成功</span></li>
                        <p style="font-size:14px;width: 100%;text-align: center;margin-top: 10px" v-if="loading ">加载中...</p>
                        <p style="font-size:14px;width: 100%;text-align: center;margin-top: 10px" v-if="noMore && this.transferList.length >= 10">没有更多了</p>
                    </ul>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar, Centerbar } from '@/components'
    import {transferList} from "../../api/center";
    export default {
        name: "Transfer",
        components: {
            Navbar,
            Centerbar
        },
        computed: {
            disabled () {
                return this.loading || this.noMore
            }
        },
        data(){
            return {
                transferList:[],
                noMore:false,
                loading:false,
                page:1
            }
        },
        mounted()
        {
            //this.getTransferList()
        },
        methods: {
            load () {
                this.loading = true
                this.getTransferList();
            },
            getTransferList()
            {
                transferList({page:this.page}).then(res => {
                    this.loading = false
                    if (res.length < 10)
                    {
                        this.noMore = true
                    }
                    if (this.page == 1)
                    {
                        this.transferList = res
                    } else
                    {
                        this.transferList = this.transferList.concat(res)
                    }
                    this.page++

                })
            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/transfer.css";
     .box {height: 100%;background: #F2F4F9;}
</style>



